<template>

  <div class="kz-tree__wrapper">
    <!-- <div class="kz-tree__top">
          <el-button size="small" icon="plus" type="primary" @click="treeAdd({ id: 0 })"></el-button>
        </div> -->
    <!--<p>{{getTreeData}}</p>-->
    <el-tree ref="kzTree"
      :data="treeData"
      :props="data.defaultProps"
      :expand-on-click-node="false"
      lazy
      :load="loadTreeNode"
      @node-click="handleNodeClick"
      :render-content="nodeRender"
      class="kz-tree">
    </el-tree>
    <!--dialog-->
    <el-dialog :title="dialog.title"
      :visible.sync="dialog.dialogVisible"
      :close-on-click-modal="false">
      <el-form :model="dialog.form"
        ref="dialogForm"
        :rules="dialog.rules">
        <el-form-item label=""
          prop="name"
          label-width="120"
          required>
          <el-input v-model="dialog.form.name"
            auto-complete="off"
            @keyup.enter.native="submitForm"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer"
        class="dialog-footer">
        <el-button @click="cancelSubmit"> </el-button>
        <el-button type="primary"
          @click="submitForm"
          :loading="dialog.submiting"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { getDirectoryList } from '../../api/api';
import modelList from './model.vue';
import ModelContent from "./index";

//  
import { mapState, mapGetters } from 'vuex'
export default {
  name: 'kz-tree',
  props: {
    data: {
      type: Object,
      required: true,
      default() {
        return {
          // tree-node
          defaultProps: {
            children: 'children',
            label: 'name',
            isLeaf: 'isLeaf'
          },
          // ajax
          // CURD
          url: {
            C: '',
            U: '',
            R: '',
            D: ''
          }
        }
      }
    }
  },
  data() {
    // node
    this.__currentNode = null;
    const validateName = (rule, value, callback) => {
      //                if (value.trim() == '') {
      //                    callback(new Error(''));
      //                }else {
      //                    callback();
      //                }
      const re = new RegExp("^[a-zA-Z0-9\u4e00-\u9fa5]+$");
      console.log(value);

      if (!value) {
        callback(new Error(''));
      } else {
        if (re.test(value)) {
          callback();
        } else {
          callback(new Error(''));
        }
      }
    };
    return {
      treeData: [],
      /*  */
      dialog: {
        title: '',
        dialogVisible: false,
        submiting: false,
        form: {
          name: '',
          id: '',
          parent_id: 0
        },
        rules: {
          name: {
            required: true,
            validator: validateName,
            //                  message: '',
            trigger: 'blur'
          }
          //              name: {
          //                  required: true,
          //                  validator : validateName,
          // //                  message: '',
          //                  trigger: 'blur'
          //              }
        }
      },
    }
  },
  computed: {
    ...mapState({
      b: state => state.b,
      a: state => state.a
    }),
    ...mapGetters(['bmsg', 'amsg', 'treeData']),
    getTreeData() {
      console.log(this.$store.state.treeDatas)
      if (this.$store.state.treeDatas != "") {
        //                  this.treeData = this.$store.state.treeDatas.data;
        //                  this.loadTreeNode(this.$store.state.treeDatas[0]);
        const url = this.data.url.R;
        const para = { parent_id: 0 };
        this.$emit("node-click", para);
        this.fetch(url, para)
          .then(data => {
            console.log(data)
            this.treeData = data;
            resolve(data)
          });

      }
    }
  },
  methods: {
    /*  */
    loadTreeNode(treeItem, resolve) {
      const url = this.data.url.R;
      const para = { parent_id: '' };
      if (treeItem.id != null && treeItem.id != "" && treeItem.id != 0 && treeItem.data.id != null) {
        para.parent_id = treeItem.data.id;
        this.$emit("node-click", para);
      } else if (treeItem.data.id == null) {
        para.parent_id = 0;
        this.$emit("node-click", para);
      } else {
        para.parent_id = treeItem.id;
        this.$emit("node-click", para);
      }
      this.fetch(url, para)
        .then(data => {
          resolve(data)
        });
    },
    /*  */
    handleNodeClick(args) {
      this.$emit('selectNode', args.id);
    },
    /* title */
    nodeRender(h, { _self, node, data }) {

      let level;
      if (node.level > 8) {
        level = 8;
      } else {
        level = node.level;
      }
      const childrenNodes = data.id === 0 ? [h('span', data.name)] : [
        h('i',

          { 'class': 'fas fa-folder', style: { 'vertical-align': 'baseline', 'color': '#F6AF39' } }
        ),

        h('span',
          {
            'class': 'treeEllipsis',
            domProps: { title: data.name },
            style: {
              width: 150 - level * 18 + 'px',
              "font-size": "14px",
              "line-height": "1",
              "height": "16px"
            }
          },
          data.name),
        h('span',
          {
            'class': 'kz-tree-bar'
          }, data.id == "-1" || data.id == "-2" ? ""
            : []
        )
      ]
      const clock = [


        h('i',

          { 'class': 'fas fa-clock ', style: { 'vertical-align': 'baseline' } }),

        h('span',
          {
            'class': 'treeEllipsis',
            domProps: { title: data.name },
            style: { width: 150 - level * 18 + 'px', "font-size": "14px" }
          },
          data.name)

      ]

      const team = [


        h('i',

          { 'class': 'fas fa-handshake ', style: { 'vertical-align': 'baseline' } }),

        h('span',
          {
            'class': 'treeEllipsis',
            domProps: { title: data.name },
            style: { width: 150 - level * 18 + 'px', "font-size": "14px" }
          },
          data.name)

      ]

      if (data.id > 0) {
        return h(
          'div',
          {
            'class': 'el-tree-node__label',
            prop: {
              children: '-'
            }
          },
          childrenNodes
        )
      } else {
        return h(
          'div',
          {
            'class': 'el-tree-node__label',
            prop: {
              children: '-'
            }
          },
          (data.parentId == 0) ? team : clock
        )
      }

      const childrenNodes1 = data.id === 0 ? [h('span', data.name)] : [
        h('span', { 'class': 'spanEllipsis' }, [h('svg', {
          'class': 'icon',
          attrs: { 'aria-hidden': 'true' },
          domProps: { title: data.name },
          style: { 'margin-right': '0.1em' }
        }, [h('use', { attrs: { 'xlink:href': '#icon-wenjianjiayigongxiang' } })]
        ), h('span', data.name)]),]


      if (!data.userName) {
        return h(
          'div',
          {
            'class': 'el-tree-node__label',
            prop: {
              children: '-'
            }
          },
          childrenNodes
        )
      } else {
        return h(
          'div',
          {
            'class': 'el-tree-node__label',
            prop: {
              children: '-'
            }
          },
          childrenNodes1
        )
      }


    },
    /*  */
    treeAdd(treeItem, event, node) {
      this.__currentNode = node
      Object.assign(this.dialog, {
        title: '',
        dialogVisible: true,
        form: {
          name: '',
          id: '',
          parent_id: treeItem.id
        }
      })
    },
    /*  */
    treeEdit(treeItem, event, node) {
      this.__currentNode = node
      Object.assign(this.dialog, {
        title: '',
        dialogVisible: true,
        form: {
          name: treeItem.name,
          id: treeItem.id,
          parent_id: treeItem.parent_id
        }
      })
    },
    /*  */
    treeDelete(treeItem, event, node) {
      const fetchDelOk = () => {
        // ajax
        const url = this.data.url.D
        this.fetch(url, { id: treeItem.id }, 'post').then(data => {
          // removeChild
          // hack
          // https://github.com/ElemeFE/element/blob/dev/packages/tree/src/model/node.js#L187
          try {
            node.parent.removeChild(node)
            const url = this.data.url.R
            this.fetch(url, node.data.parentId)
          } catch (err) {
            console.error(err)
          }
          // 
          this.$message({ message: '', type: 'success', duration: 2000 })
        })
      }
      // 
      this.$confirm('?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(fetchDelOk).catch(e => e)
    },
    /* ajax */
    fetch(url, data, type = 'POST') {
      const success = (data, resolve, reject) => {
        const filterData = new Array();
        if (data.status === 1) {
          for (const m in data.data) {
            if (data.data[m].id == -1 || data.data[m].id == -2) {
            } else {
              filterData.push(data.data[m])
            }
          }
          resolve(filterData)
        } else {
          //     console.error(data.data.code+":"+ data.data.message)
          reject(data)
        }
      }
      return new Promise((resolve, reject) => {
        if (type.toLowerCase() === 'post') {
          if (data) {
            var dataUrl = Object.keys(data).map(item => item + "=" + data[item])
            url = url + (url.indexOf('?') > -1 ? '' : '?') + dataUrl.join('&')
          }
          this.$http.post(url)
            .then(res => res)
            .then(data => success(data.data, resolve, reject))
        } else {
          if (data) {
            var dataUrl = Object.keys(data).map(item => item + "=" + data[item])
            url = url + (url.indexOf('?') > -1 ? '' : '?') + dataUrl.join('&')
          }

          this.$http.get(url)
            .then(res => res)
            .then(data => success(data.data, resolve, reject),
          )
        }
      })
    },
    /* ######## */
    /*  */
    cancelSubmit() {
      this.dialog.dialogVisible = false
      this.$refs.dialogForm.resetFields()
    },
    submitForm() {
      this.$refs.dialogForm.validate((valid) => {
        if (valid) { // 
          if (this.$refs.dialogForm.model.parent_id == null) {
            this.$refs.dialogForm.model.parent_id = -1;
          }
          const checkDirNameUrl = '/api/directory/checkDirName?parentId=' + this.__currentNode.data.parentId + '&dirName=' + this.$refs.dialogForm.model.name + '&dirId=' + this.$refs.dialogForm.model.id +
            '&dirParentId=' + this.$refs.dialogForm.model.parent_id + '&userName=' + this.$store.state.userInfo.name
          const _this = this;
          _this.$http.post(checkDirNameUrl)
            .then(function (response) {
              if (response.data.state == 1) {
                _this.fetchAddTreeNode()
              } else {
                _this.$message({
                  message: '',
                  type: 'warning',
                  duration: 2000
                });
              }
            })
            .catch(function (error) {
              console.log(error)
              _this.$message({
                message: '',
                type: 'warning',
                duration: 2000
              });
            })
        } else {
          return false
        }
      })
    },
    fetchAddTreeNode() {
      const url = this.dialog.form.id ? this.data.url.U : this.data.url.C
      this.dialog.submiting = true
      this.fetch(url, this.dialog.form, 'post')
        .then(data => {
          /* dialog */
          Object.assign(this.dialog, {
            submiting: false,
            dialogVisible: false
          })
          this.$refs.dialogForm.resetFields()
          /*  */
          const message = this.dialog.form.id ? '' : ''
          this.$message({ message: message, type: 'success' })

          if (this.dialog.form.id) { // 
            this.__currentNode && this.$set(this.__currentNode, 'data', data)
          } else { // 
            /* treeNode api */
            if (this.__currentNode) { // 
              this.__currentNode.doCreateChildren([data])
            } else if (data.parentId === "0") { // 
              this.$refs.kzTree.root.doCreateChildren([data])
            }
          }
        })
    },
  },
  components: {
    modelList,
    ModelContent
  },

}
</script>

<style scoped>
.kz-tree__wrapper {
  min-width: 250px;
  text-align: left;
}

.kz-tree__top {
  padding: 10px;
  text-align: right;
  border-bottom: 1px solid #ddd;
}

.el-tree-node {
  position: relative;
}

.el-tree-node__content:hover .kz-tree-bar {
  display: block;
}

.kz-tree-bar {
  display: none;
  position: absolute;
  top: 0;
  right: 10px;
  font-size: 12px;
}

.kz-tree-bar > i {
  margin: 0 5px;
  color: #999;
}

.kz-tree-bar > i:hover {
  color: #20a0ff;
}

.el-tree {
  border: none;
}

.el-tree-node__label {
  width: 120px;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}

/* .iconfont {
  font-family: "iconfont";
  font-size: 24px;
  font-style: normal;
} */
</style>
